import { SetErrors } from '../../../..'
import { CONTENT_LIST } from '../../../constants/content-list'
import { ERROR_TYPE } from '../../../errors'
import { checkAdditionalItems } from './checkAdditionalItems'
import { checkChapterItem } from './checkChapterItem'
import { getChapterNumber } from './getChapterNumber'
import { getChaptersData } from './getChaptersData'
import { getIsCapitalize } from './getIsCapitalize'
import { getIsDotInEnd } from './getIsDotInEnd'
import { getIsUpperCase } from './getIsUpperCase'

export type checkListReturnType = {
  rule2?: string[]
  rule3?: string[]
  rule4?: string[]
  rule5?: string[]
  rule6?: string[]
  rule7?: string[]
  rule8?: string[]
  rule9?: string[]
  rule10?: string[]
  rule10_1?: string[]
}

export const checkList = (setErrors: SetErrors, list: string[], constants: typeof CONTENT_LIST) => {
  const chapters = getChaptersData(list, constants.chapter, constants.conclusion)

  const chaptersEndPosition = checkAdditionalItems(setErrors, list, chapters, constants)

  chapters.forEach(({ chapter, conclusion, chapterPosition, conclusionPosition }, chapterIndex, arr) => {
    const chapterNumber = getChapterNumber(chapter, constants.chapter)

    if (!getIsUpperCase(chapter)) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule2', chapter)
    }

    if (chapterNumber && chapterNumber !== (chapterIndex + 1).toString()) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule3', chapter)
    }

    if (conclusion && !getIsCapitalize(conclusion)) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule4', conclusion)
    }

    if (chapterNumber && !getIsDotInEnd(chapterNumber)) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule7', chapter)
    }

    if (!conclusion) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule8', chapter)
    }

    const items = list.slice(chapterPosition + 1, conclusionPosition ?? chaptersEndPosition)

    if (items.length < 3 || items.length > 5) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule5', (chapterIndex + 1).toString())
    }

    items.forEach((item, index) => {
      const { isNumberCorrect, isTileCapitalize } = checkChapterItem(item, chapterIndex + 1, index + 1)
      if (!isNumberCorrect) {
        setErrors(ERROR_TYPE.pagesFidelity, 'rule6', item)
      }
      if (!isTileCapitalize) {
        setErrors(ERROR_TYPE.pagesFidelity, 'rule4', item)
      }
    })
  })
}
