import { PDFDocumentProxy } from 'pdfjs-dist'
import { SetErrors } from '../../..'
import { CONTENT_LIST } from '../../constants/content-list'
import { getContentListPageNumber } from '../../helpers/getRefListPageNumber'
import { getPageString } from '../../utils/parsePdfUtils'
import { ContentListConfigType } from '../checkPagesFidelity'
import { checkAfterReferenceSymbols } from './helpers/checkAfterReferenceSymbols'
import { checkInsideReferenceSymbols } from './helpers/checkInsideReferenceSymbols'
import { checkIsUsed } from './helpers/checkIsUsed'
import { checkOrder } from './helpers/checkOrder'
import { checkStarrSymbols } from './helpers/checkStarrSymbols'
import { findReferenceMatches } from './helpers/findReferenceMatches'
import { getTransformedReference } from './helpers/getTransformedReference'
import { trimAndReplaceDashes } from './helpers/trimAndReplaceDashes'

/**
 errors:
 rule5: 'Літературні джерела мають бути розташовані в порядку зростання. Розпізнані номери: {str1}',
 rule6: 'Літературні джерела які немають посилань: {str1}' 
 * @param arr 
 * @param setErrors 
 */
export const checkReferenceFidelity = (arr: string[], setErrors: SetErrors, limit: number): void => {
  const list = arr.map((item) => item.slice(3, item.length - 2)).join(',')
  const references = trimAndReplaceDashes(list)
  const numbers: number[] = getTransformedReference(references)
  checkOrder(list, numbers, setErrors)
  checkIsUsed(limit, numbers, setErrors)
}

export const checkReferenceOrder = async (
  pdfData: PDFDocumentProxy,
  contentListConfig: ContentListConfigType,
  length: number,
  setErrors: SetErrors
): Promise<void> => {
  if (!contentListConfig.length || !length) {
    console.log('Impossible to check reference order, contentListConfig is empty')
    return
  }

  const start = contentListConfig[0].page
  const end = getContentListPageNumber(contentListConfig, CONTENT_LIST.litList) as number

  const fullText: string[] = []
  for (let i = start; i < end; i++) {
    const text = await getPageString(pdfData, i)
    fullText.push(text)
  }
  const arr = findReferenceMatches(fullText.join(' '))
  arr.forEach((item) => {
    checkStarrSymbols(item, setErrors)
    checkInsideReferenceSymbols(item, setErrors)
    checkAfterReferenceSymbols(item, setErrors)
  })
  checkReferenceFidelity(arr, setErrors, length)
}
