/**
 * @param title - chapter title
 * @param originalTitle - original chapter title
 * @returns [isTitle, isTitleReg] - is title equal to original title and is title equal to original title in uppercase
 */
export const getIsChapterValid = (title: string, originalTitle: string): [boolean, boolean] => {
  const isTitle = title.toLowerCase() === originalTitle.toLowerCase()
  const isTitleReg = title === originalTitle.toUpperCase()

  return [isTitle, isTitleReg]
}
