import { findIndexByMatch } from './findIndexByMatch'

/**
 *
 * @param rows  - array of strings
 * @param regFirst - first regex
 * @param regLast - last regex
 * @returns - index of first match
 */
export const findPageMatchByCase = (rows: string[], regFirst: RegExp, regLast: RegExp): number => {
  const index1 = findIndexByMatch(rows, regFirst)
  if (index1 !== -1) {
    return index1
  }

  const index2 = findIndexByMatch(rows, regLast)
  if (index2 !== -1) {
    return index2
  } else {
    return -1
  }
}
