import { SetErrors } from '../../../..'
import { ERROR_TYPE } from '../../../errors'

export function checkReferenceNumberFormat(setErrors: SetErrors, referenceItem: string): void {
  // Перевірка, чи містить номер джерела у форматі "цифра - крапка - пробіл"
  const regex = /^\d\. /
  if (!regex.test(referenceItem)) {
    setErrors(ERROR_TYPE.referenceList, 'rule3', referenceItem)
  }
}
