import { SetErrors } from '../../../..'
import { ERROR_TYPE } from '../../../errors'

export const checkIsUsed = (limit: number, numbers: number[], setErrors: SetErrors): void => {
  const allNumbers = Array.from(Array(limit + 1).keys()).slice(1)
  const notRepeatedNumbers = numbers.filter((number, index) => numbers.indexOf(number) === index)
  const missingNumbers = allNumbers.filter((number) => notRepeatedNumbers.indexOf(number) === -1)

  if (missingNumbers.length > 0) {
    setErrors(ERROR_TYPE.referenceOrder, 'rule6', missingNumbers.join(', '))
  }
}
