import { SetErrors } from '../../../..'
import { CONTENT_LIST } from '../../../constants/content-list'
import { ERROR_TYPE } from '../../../errors'
import { findIndexByMatch } from './findIndexByMatch'
import { ChapterTitleType } from './getChaptersData'
import { getIsChapterValid } from './getIsChapterValid'
import { getIsUpperCase } from './getIsUpperCase'

export const checkAdditionalItems = (
  setErrors: SetErrors,
  list: string[],
  chapters: ChapterTitleType[],
  constants: typeof CONTENT_LIST
): number => {
  let chaptersEndPosition
  const abstractReg = new RegExp(constants.abstract, 'i')
  const abstractEnReg = new RegExp(constants.abstractEng, 'i')
  const introductionReg = new RegExp(constants.introduction, 'i')
  const litListReg = new RegExp(constants.litList, 'i')
  const shortNamesReg = new RegExp(constants.shortNames, 'i')
  const additionReg = new RegExp(constants.addition, 'i')

  const abstractIndex = findIndexByMatch(list, abstractReg)
  const abstractEnIndex = findIndexByMatch(list, abstractEnReg)
  const introductionIndex = findIndexByMatch(list, introductionReg)
  const litListIndex = findIndexByMatch(list, litListReg)
  const shortNamesIndex = findIndexByMatch(list, shortNamesReg)
  const additionIndex = findIndexByMatch(list, additionReg)
  const isAbstract = abstractIndex !== -1
  const isAbstractEn = abstractEnIndex !== -1
  const isIntroduction = introductionIndex !== -1
  const isLitList = litListIndex !== -1
  const isShortNames = shortNamesIndex !== -1
  const isAddition = additionIndex !== -1

  const checkTitleRegister = (title: string) => {
    if (!getIsUpperCase(title)) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule2', title)
    }
  }

  if (!isAbstract) {
    setErrors(ERROR_TYPE.pagesFidelity, 'rule10', constants.abstract)
  } else {
    checkTitleRegister(list[abstractIndex])
    if (abstractIndex !== 0) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule10_1', constants.abstract)
    }
  }

  if (!isAbstractEn) {
    setErrors(ERROR_TYPE.pagesFidelity, 'rule10', constants.abstractEng)
  } else {
    checkTitleRegister(list[abstractEnIndex])
    if (isAbstract && abstractIndex > abstractEnIndex) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule10_1', constants.abstractEng)
    }
  }

  if (!isIntroduction) {
    setErrors(ERROR_TYPE.pagesFidelity, 'rule10', constants.introduction)
  } else if (
    (isAbstract && abstractIndex > introductionIndex) ||
    (isAbstractEn && abstractEnIndex > introductionIndex)
  ) {
    setErrors(ERROR_TYPE.pagesFidelity, 'rule10_1', constants.introduction)
  }

  const lastChapterConclusionIndex = chapters[chapters.length - 1].conclusionPosition
  if (!lastChapterConclusionIndex) {
    setErrors(ERROR_TYPE.pagesFidelity, 'rule8', chapters[chapters.length - 1].chapter)
    setErrors(ERROR_TYPE.pagesFidelity, 'rule10')
  } else {
    chaptersEndPosition = lastChapterConclusionIndex
    const conclusionIndex = lastChapterConclusionIndex + 1
    const conclusion = list[conclusionIndex]
    const [isConclusion, isConclusionReg] = getIsChapterValid(conclusion, constants.conclusion)
    if (!isConclusionReg && !isConclusion) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule10')
    } else if (!isConclusionReg) {
      setErrors(ERROR_TYPE.pagesFidelity, 'rule2', conclusion)
    }
  }

  if (!isLitList) {
    setErrors(ERROR_TYPE.pagesFidelity, 'rule10', constants.litList)
  } else {
    checkTitleRegister(list[litListIndex])
  }

  if (isShortNames) {
    checkTitleRegister(list[shortNamesIndex])
  }

  if (isAddition) {
    checkTitleRegister(list[additionIndex])
  }

  if (!chaptersEndPosition) {
    chaptersEndPosition = isLitList ? litListIndex - 1 : isAddition ? additionIndex - 1 : list.length - 1
  }

  return chaptersEndPosition
}
